@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  font-family: 'Roboto', sans-serif;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

.back {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
}

* {
    padding: 0;
}

body {
    overflow-x: hidden;
}

.bodyBackground::after {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color:  rgba(0,0,0,0.8);
    height: 100vh;
    z-index: 1000;
}


.backbtn {
    background-color: #0d6efd;
    border-radius: 5px;
    color: #ffffff;
}

@media only screen and (max-width: 550px){
    .backbtn {
        display: block;
        width: 100%;
        text-align: center;
    }
}
